.inputbox{
    display: grid;
    place-items: center;
    height: 100vh;
    background-color: #f8f8f8;
}

.inputBox_field{
    padding: 10%;
    text-align: center;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 5px 6px rgba(0, 0, 0, 0.12), 0 2px 3px rgba(0, 0, 0, 0.24);
    padding-top: 0;
    padding-bottom: 50px;
    margin: 20px;
}

.inputBox_field > h1 {
    margin: 20px;
}

.inputBox_field >form> button {
    margin-top: 30px;
    text-transform: inherit !important;
}

.inputBox_field >form> p {
   color: red;
}